.spinner {
    width: 40px;
    height: 40px;
    border: 3px solid #F5F6F8;
    border-top: 3px solid #447BBA;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
} 